body {
  width: 100vw;
  height: 100vh;
  background-color: #FAFAFA;
  overflow-x: hidden;
}

.navAText {
  margin-left: 203px;
  font-size: 20px;
}

.interFont {
  font-family: 'Inter', sans-serif;
}

.popUpperText {
  letter-spacing: 0.175rem;
}


.headerNavBar {
font-family: "Poppins", sans-serif;
}


@keyframes slide {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

.animate-slide {
  animation: slide 500ms ease-in-out forwards;
}